<template lang="html">
  <h1>Investigating corporate landlords</h1>
  <div class="intro">
    <p>Corporate landlords are companies that buy, sell, own and rent out homes for profit. They are thought to be the biggest private landlords in many European cities, and in a time of housing crises across Europe we believe it's important to know what role corporate landlords play in and what their impact is on housing markets. To get a comprehensive and informative view of this topic, we believe we need to look at the data at the city level and that these data are required:</p>
    <ul>
    <li>Evolution of capital flows into housing markets across cities and countries.</li>
    <li>Evolution of the number of homes bought, sold and owned by corporate landlords across cities.</li>
    <li>Addresses and date of acquisition of homes owned by corporate landlords in a given city.</li>
    <li>Evolution over a periof of time in a given city of:
    <ul>
    <li>the population and the number of homes;</li>
    <li>the share of home ownership by individuals, companies, other organisations, and the state;</li>
    <li>the share of home ownership by type of company (investment fund, REIT, bank, housing corporation...);</li>
    <li>the share of home-owner-occupiers and tenants;</li>
    <li>rental and property prices;</li>
    <li>the share of household income devoted to rent (or generally to housing);</li>
    <li>the share of people overburdened by housing costs.</li>
    </ul></li>
    </ul>
    <p>There are additional data that may contribute to get a more in-depth view:</p>
    <ul>
    <li>housing regulations and tax regimes in place affecting corporate landlords, housing investment and the property and rental markets;</li>
    <li>and the financial results of corporate landlords over time.</li>
    </ul>
    <p>While national averages and some types of data at the country level are easily accessible, data at the city level is generally much less available, which means that we may need to compile some of the datasets ourselves, as we started doing with the project <a href="https://cities4rent.journalismarena.media">Cities for Rent: Investigating Corporate Landlords Across Europe</a>. If you are interested in contributing to that project, do get in touch.</p>
</div>
